'use client';

import React, { useState, useMemo } from 'react';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import RecipeCard from '@/components/RecipeCard';
import recipes from '@/../public/recipes.json';

export default function ReceitasPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedTag, setSelectedTag] = useState('');
  const [sortBy, setSortBy] = useState('titulo');

  // Extrai todas as categorias e tags √∫nicas
  const allCategories = Array.from(
    new Set(recipes.recipes.flatMap((r) => r.category))
  );

  const allTags = Array.from(new Set(recipes.recipes.flatMap((r) => r.tags)));

  // Filtra receitas
  const filteredRecipes = useMemo(() => {
    let result = [...recipes.recipes];

    // Filtro por busca
    if (searchTerm) {
      result = result.filter(
        (recipe) =>
          recipe.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          recipe.story.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Filtro por categoria
    if (selectedCategory) {
      result = result.filter((recipe) =>
        recipe.category.includes(selectedCategory)
      );
    }

    // Filtro por tag
    if (selectedTag) {
      result = result.filter((recipe) => recipe.tags.includes(selectedTag));
    }

    // Ordena√ß√£o
    if (sortBy === 'titulo') {
      result.sort((a, b) => a.title.localeCompare(b.title));
    } else if (sortBy === 'dificuldade') {
      const difficultyOrder = { F√°cil: 1, Intermedi√°rio: 2, Avan√ßado: 3 };
      result.sort(
        (a, b) =>
          (difficultyOrder[a.difficulty as keyof typeof difficultyOrder] || 0) -
          (difficultyOrder[b.difficulty as keyof typeof difficultyOrder] || 0)
      );
    }

    return result;
  }, [searchTerm, selectedCategory, selectedTag, sortBy]);

  return (
    <div className="min-h-screen flex flex-col bg-gradient-to-b from-pink-50 to-white">
      <Header />

      {/* Hero */}
      <section className="py-12 px-4 text-center bg-gradient-to-r from-pink-100 to-amber-100">
        <h1 className="text-5xl font-bold text-amber-900 mb-4">üç∞ Todas as Receitas</h1>
        <p className="text-xl text-amber-800">
          Explore {recipes.recipes.length} hist√≥rias m√°gicas em forma de bolo
        </p>
      </section>

      <div className="flex-1 py-12 px-4">
        <div className="max-w-7xl mx-auto">
          {/* Controles de Filtro */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-12">
            {/* Busca */}
            <input
              type="text"
              placeholder="Buscar receita..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="px-4 py-3 rounded-lg border-2 border-pink-200 focus:outline-none focus:border-pink-500"
            />

            {/* Categoria */}
            <select
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
              className="px-4 py-3 rounded-lg border-2 border-pink-200 focus:outline-none focus:border-pink-500 bg-white"
            >
              <option value="">Todas as categorias</option>
              {allCategories.map((cat) => (
                <option key={cat} value={cat}>
                  {cat}
                </option>
              ))}
            </select>

            {/* Tag */}
            <select
              value={selectedTag}
              onChange={(e) => setSelectedTag(e.target.value)}
              className="px-4 py-3 rounded-lg border-2 border-pink-200 focus:outline-none focus:border-pink-500 bg-white"
            >
              <option value="">Todas as tags</option>
              {allTags.map((tag) => (
                <option key={tag} value={tag}>
                  {tag}
                </option>
              ))}
            </select>

            {/* Ordena√ß√£o */}
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="px-4 py-3 rounded-lg border-2 border-pink-200 focus:outline-none focus:border-pink-500 bg-white"
            >
              <option value="titulo">Ordenar por t√≠tulo</option>
              <option value="dificuldade">Ordenar por dificuldade</option>
            </select>
          </div>

          {/* Resultados */}
          {filteredRecipes.length > 0 ? (
            <>
              <p className="text-amber-800 font-semibold mb-8">
                Mostrando {filteredRecipes.length} receita{filteredRecipes.length !== 1 ? 's' : ''}
              </p>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {filteredRecipes.map((recipe) => (
                  <RecipeCard
                    key={recipe.id}
                    id={recipe.id}
                    title={recipe.title}
                    slug={recipe.slug}
                    image={recipe.image}
                    difficulty={recipe.difficulty}
                    time={recipe.time}
                    servings={recipe.servings}
                    tags={recipe.tags}
                  />
                ))}
              </div>
            </>
          ) : (
            <div className="text-center py-20">
              <p className="text-2xl text-amber-900 font-bold mb-4">üò¢ Nenhuma receita encontrada</p>
              <p className="text-amber-700 mb-8">
                Tente ajustar seus filtros para encontrar a hist√≥ria perfeita.
              </p>
              <button
                onClick={() => {
                  setSearchTerm('');
                  setSelectedCategory('');
                  setSelectedTag('');
                }}
                className="bg-pink-500 hover:bg-pink-600 text-white font-bold py-3 px-8 rounded-lg transition-colors"
              >
                ‚ú® Limpar Filtros
              </button>
            </div>
          )}
        </div>
      </div>

      <Footer />
    </div>
  );
}
