import React from 'react';
import Link from 'next/link';
import { Clock, Users, ChefHat } from 'lucide-react';

interface RecipeCardProps {
  id: number;
  title: string;
  slug: string;
  image: string;
  difficulty: string;
  time: string;
  servings: string;
  tags: string[];
}

export default function RecipeCard({ id, title, slug, image, difficulty, time, servings, tags }: RecipeCardProps) {
  return (
    <Link href={`/receita/${slug}`}>
      <div className="group cursor-pointer bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden hover:scale-105">
        {/* Imagem */}
        <div className="relative h-64 bg-gradient-to-b from-amber-100 to-pink-100 overflow-hidden">
          <img
            src={image}
            alt={title}
            className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
          />
          <div className="absolute top-4 right-4 bg-pink-500 text-white px-3 py-1 rounded-full text-xs font-bold">
            {difficulty}
          </div>
        </div>

        {/* Conte√∫do */}
        <div className="p-5">
          <h3 className="text-xl font-bold text-amber-900 mb-3 line-clamp-2 group-hover:text-pink-600 transition-colors">
            {title}
          </h3>

          {/* Info R√°pida */}
          <div className="flex gap-4 mb-4 text-sm text-amber-700">
            <div className="flex items-center gap-1">
              <Clock size={16} />
              <span>{time}</span>
            </div>
            <div className="flex items-center gap-1">
              <Users size={16} />
              <span>{servings}</span>
            </div>
          </div>

          {/* Tags */}
          <div className="flex gap-2 flex-wrap mb-4">
            {tags.slice(0, 3).map((tag, idx) => (
              <span key={idx} className="bg-pink-100 text-pink-700 px-2 py-1 rounded text-xs font-semibold">
                #{tag}
              </span>
            ))}
          </div>

          {/* CTA */}
          <div className="inline-block text-pink-600 font-bold text-sm group-hover:text-pink-700">
            Ver Receita ‚Üí
          </div>
        </div>
      </div>
    </Link>
  );
}
